.back-call {

    &__title {
        font-weight: 700;
        font-size: 35px;
        line-height: math.div(45, 35);

        span {
            color: $tropazSecondaryColor;
        }
        @media (max-width:$smallTablet){
             font-size: 30px; 
        }
    }

    &__form {
        margin-top: 14px;
    }

    @media (min-width:$tablet) {
        width: 562px;
    }
}

.back-call-form {
    &__label {
        display: block;

        &:not(:first-child) {
            margin-top: 20px;
        }
    }

    &__name {
        display: block;
        font-size: 14px;
        line-height: math.div(20, 14);
    }

    &__input {
        margin-top: 4px;
        background-color: $whiteColor;
        border: 1px solid #eee;
        border-radius: 3px;
        height: 44px;
        width: 100%;
        padding: 0 20px 0 10px;

        &:focus {
            border: 1px solid $tropazSecondaryColor;
            outline-color: $tropazSecondaryColor;
        }
    }

    .just-validate-error-field {
        border: 2px solid $tropazSecondaryColor;
    }

    &__btn {
        margin: 0 auto;
        margin-top: 30px;
        padding: 17px 48px 16px;
        text-align: center;
        display: block;
        @media (max-width:$mobile){
             width: 100%; 
        }
    }
}
